import portfolioService from '@/services/portfolioService'

const INITIAL_PORTFOLIO = {
  id: null,
  stats: null,
  assets: null,
  transactions: null,
}

const state = {
  selectedPortfolio: INITIAL_PORTFOLIO,
  totalPortfolios: INITIAL_PORTFOLIO,
}

const getters = {
  // Selected Portfolio (SP)
  getSPStats(state) {
    return state.selectedPortfolio.stats
  },
  getSPAssets(state) {
    return state.selectedPortfolio.assets
  },
  getSPTransactions(state) {
    const portfolio = state.selectedPortfolio
    return portfolio.transactions[portfolio.transactionsPage]
  },
  // Total Portfolios (TP)
  getTPStats(state) {
    return state.totalPortfolios.stats
  },
  getTPAssets(state) {
    return state.totalPortfolios.assets
  },
}

const mutations = {
  // Selected Portfolio (SP)
  selectPortfolio(state, payload) {
    if (!payload) {
      state.selectedPortfolio = INITIAL_PORTFOLIO
    } else if (state.selectedPortfolio.id !== payload) {
      state.selectedPortfolio.id = payload
      state.selectedPortfolio.stats = INITIAL_PORTFOLIO.stats
      state.selectedPortfolio.assets = INITIAL_PORTFOLIO.assets
      state.selectedPortfolio.transactions = INITIAL_PORTFOLIO.transactions
    }
  },
  setSPDetails(state, payload) {
    var selectedPortfolio = state.selectedPortfolio
    if (selectedPortfolio.id === payload.id) {
      selectedPortfolio.stats = payload.stats
      selectedPortfolio.assets = payload.assets
    } else {
      console.error('Selected portfolio is different.')
    }
  },
  setSPTransactions(state, payload) {
    var selectedPortfolio = state.selectedPortfolio
    if (selectedPortfolio.id === payload.id) {
      selectedPortfolio.transactions = selectedPortfolio.transactions || []
      selectedPortfolio.transactions[payload.page] = selectedPortfolio.transactions
    } else {
      console.error('Selected portfolio is different.')
    }
  },
  // Total Portfolios (TP)
  setTotalPortfolios(state, payload) {
    state.totalPortfolios = payload
  },
  // !! FOR TESTING PURPOSES ONLY
  deleteAllTransactions(state) {
    state.selectedPortfolio.transactions = null
    state.totalPortfolios.transactions = null
  },
}

const actions = {
  // Selected Portfolio (SP)
  loadPortfolioDetails({ commit }, portfolioId) {
    commit('selectPortfolio', portfolioId)
    return portfolioService.getPortfolioDetails(portfolioId)
      .then(data => {
        commit('setSPDetails', data)
        return data
      })
      .catch(errMsg => Promise.reject(errMsg))
  },
  loadPortfolioTransactions({ commit, state }, {portfolioId, page, itemsPerPage}) {
    page = page || 1
    itemsPerPage = itemsPerPage || 20
    const offset = itemsPerPage * (page - 1)
    const limit = offset + itemsPerPage

    commit('selectPortfolio', portfolioId)

    return portfolioService.getPortfolioTransactions(portfolioId, offset, limit)
      .then(transactions => {
        let payload = {
          id: portfolioId,
          transactions,
          page,
        }
        commit('setSPTransactions', payload)
        return payload
      })
      .catch(errMsg => Promise.reject(errMsg))
  },
  // Total Portfolios (TP)
  loadTotalPortfolios({ commit }) {
    return portfolioService.getTotalPortfolios()
      .then(data => {
        commit('setTotalPortfolios', data)
        return data
      })
      .catch(errObj => Promise.reject(errObj))
  },
  // !! FOR TESTING PURPOSES ONLY
  deleteAllTransactions({ commit }) {
    return portfolioService.deleteAllTransactions()
      .then(() => {
        commit('deleteAllTransactions')
      })
  },
}

export default {
  state,
  mutations,
  actions,
  getters,
}



